h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: "Poppins", sans-serif;
}

.page {
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
}

.page__body {
  background-color: #1d1d1d;
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 92px;
}

@media (min-width: 600px) {
  .page__section {
    margin-bottom: 120px;
  }
}

@media (min-width: 1024px) {
  .page__section {
    margin-bottom: 160px;
  }
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  text-transform: capitalize;
  color: #fff;
  margin: 0 0 48px;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
}

@media (min-width: 600px) {
  .page__section-title {
    font-size: 48px;
    line-height: 48px;
  }
}

@media (min-width: 1024px) {
  .page__section-title {
    font-size: 64px;
    line-height: 64px;
  }
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: block;
}

.icon__tooltip {
  letter-spacing: 2px;
  color: #fff;
  text-transform: uppercase;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 12px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  transition: opacity .3s;
  display: block;
  position: relative;
  top: 50%;
  transform: translate(-100%, -50%);
}

.icon:hover .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}

.icon--menu {
  background-image: url("menu.3b0bcde6.svg");
}

.icon--menu:hover {
  background-image: url("open.13e539ab.svg");
}

.icon--phone {
  background-image: url("phone.bc713610.svg");
}

.icon--cross {
  background-image: url("cross.01957458.svg");
  grid-column: 3 / 4;
  width: 16px;
  height: 16px;
}

@media (min-width: 1024px) {
  .icon--cross {
    width: 22px;
    height: 22px;
  }
}

